import { formatFileSize, isDefinedGlobally } from './utils';

const messages = {
  _default: (field) => `O valor invlido.`,
  after: (field, [target]) => `O valor deste campo deve estar depois do campo ${target}.`,
  alpha: (field) => `O valor deste campo deve conter somente letras.`,
  alpha_dash: (field) => `O valor deste campo deve conter letras, nmeros e traos.`,
  alpha_num: (field) => `O valor deste campo deve conter somente letras e nmeros.`,
  alpha_spaces: (field) => `O valor deste campo s pode conter caracteres alfabticos e espaos.`,
  before: (field, [target]) => `O valor deste campo deve estar antes do campo ${target}.`,
  between: (field, [min, max]) => `O valor deste campo deve estar entre ${min} e ${max}.`,
  confirmed: (field, [confirmedField]) => `Os campos ${field} e ${confirmedField} devem ser iguais.`,
  credit_card: (field) => `O valor deste campo  invlido.`,
  date_between: (field, [min, max]) => `O valor deste campo deve estar entre ${min} e ${max}.`,
  date_format: (field, [format]) => `O valor deste campo deve estar no formato ${format}.`,
  decimal: (field, [decimals = '*'] = []) => `O valor deste campo deve ser numrico e deve conter ${!decimals || decimals === '*' ? '' : decimals} casas decimais.`,
  digits: (field, [length]) => `O valor deste campo deve ser numrico e ter exatamente ${length} dgitos.`,
  dimensions: (field, [width, height]) => `O valor deste campo deve ter ${width} pixels de largura por ${height} pixels de altura.`,
  email: (field) => `O valor deste campo deve ser um email vlido.`,
  excluded: (field) => `O valor deste campo deve ser um valor vlido.`,
  ext: (field) => `O valor deste campo deve ser um arquivo vlido.`,
  image: (field) => `O valor deste campo deve ser uma imagem.`,
  included: (field) => `O valor deste campo deve ter um valor vlido.`,
  integer: (field) => `O valor deste campo deve ser um nmero inteiro.`,
  ip: (field) => `O valor deste campo deve ser um endereo IP vlido.`,
  length: (field, [length, max]) => {
    if (max) {
      return `O tamanho deste campo est entre ${length} e ${max}.`;
    }

    return `O tamanho deste campo deve ser ${length}.`;
  },
  max: (field, [length]) => `O valor deste campo no deve ter mais que ${length} caracteres.`,
  max_value: (field, [max]) => `O valor deste campo precisa ser ${max} ou menor.`,
  mimes: (field) => `O valor deste campo deve ser um tipo de arquivo vlido.`,
  min: (field, [length]) => `O valor deste campo deve conter pelo menos ${length} caracteres.`,
  min_value: (field, [min]) => `O valor deste campo precisa ser ${min} ou maior.`,
  numeric: (field) => `O valor deste campo deve conter apenas nmeros`,
  regex: (field) => `O valor deste campo possui um formato invlido.`,
  required: (field) => `O valor deste campo  obrigatrio.`,
  size: (field, [size]) => `O valor deste campo deve ser menor que ${formatFileSize(size)}.`,
  url: (field) => `O valor deste campo no  uma URL vlida.`
};

const locale = {
  name: 'pt_BR',
  messages,
  attributes: {}
};

if (isDefinedGlobally()) {
  VeeValidate.Validator.localize({ [locale.name]: locale });
}

export default locale;
